import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useTheme, styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import TableHead from '@mui/material/TableHead';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableFooter from '@mui/material/TableFooter';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import FirstPageIcon from '@mui/icons-material/FirstPage';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import LastPageIcon from '@mui/icons-material/LastPage';
import { Grid, Toolbar, Button, Typography, CircularProgress } from '@mui/material';
// import Link from 'next/link';
import { Link, useNavigate } from 'react-router-dom';
import { Visibility } from '@mui/icons-material';
// import { useRouter } from 'next/router';
import axios from 'axios';
import Cookies from 'js-cookie';
import SearchIcon from '@mui/icons-material/Search';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.primary.dark,
        color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
    },
}));

function TablePaginationActions(props) {
    const theme = useTheme();
    const { count, page, rowsPerPage, onPageChange } = props;

    const handleFirstPageButtonClick = (event) => {
        onPageChange(event, 0);
    };

    const handleBackButtonClick = (event) => {
        onPageChange(event, page - 1);
    };

    const handleNextButtonClick = (event) => {
        onPageChange(event, page + 1);
    };

    const handleLastPageButtonClick = (event) => {
        onPageChange(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
    };


    return (
        <Box sx={{ flexShrink: 0, ml: 2.5 }}>
            <IconButton
                onClick={handleFirstPageButtonClick}
                disabled={page === 0}
                aria-label="first page"
            >
                {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
            </IconButton>
            <IconButton
                onClick={handleBackButtonClick}
                disabled={page === 0}
                aria-label="previous page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            </IconButton>
            <IconButton
                onClick={handleNextButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="next page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </IconButton>
            <IconButton
                onClick={handleLastPageButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="last page"
            >
                {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
            </IconButton>
        </Box>
    );
}

TablePaginationActions.propTypes = {
    count: PropTypes.number.isRequired,
    onPageChange: PropTypes.func.isRequired,
    page: PropTypes.number.isRequired,
    rowsPerPage: PropTypes.number.isRequired,
};





export default function All_property() {
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(5);
    const [propertyData, setPropertyData] = useState([])
    const [searchTerm, setSearchTerm] = useState("")
    const [filterData, setFilteData] = useState([])
    // Avoid a layout jump when reaching the last page with empty rows.
    const emptyRows =
        page > 0 ? Math.max(0, (1 + page) * rowsPerPage - propertyData.length) : 0;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const accessToken = Cookies.get("accessToken")
    const companyType = Cookies.get("companyName")
    // const router = useRouter()
    const navigate = useNavigate()
    const [loader, setLoader] = useState(true)
    useEffect(() => {
        const userAccessToken = Cookies.get("accessToken")
        if (!userAccessToken) {
            // router.push('/')
            navigate('/')
        }
    }, [navigate])
    const getProperty = () => {
        axios.get(`${process.env.REACT_APP_NEXT_PUBLIC_BASE_URL}/get-property-list/?get_company_type=${companyType}`,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                    'Content-Type': 'application/json'
                }
            }
        )
            .then((res) => {
                console.log("all property res", res.data.response)
                if (res) {
                    setPropertyData(res.data.response)
                    setFilteData(res.data.response)
                    setLoader(false)
                }
            })
            .catch((err) => {
                console.log("all property err", err)
            })
    }
    useEffect(() => {
        getProperty()
    }, [navigate])

    const handleSearch = (e) => {
        setSearchTerm(e.target.value)
        if (e.target.value.length > 0) {
            const searchData = propertyData.filter((val) => val.property_name.toLocaleLowerCase().includes(e.target.value.toLocaleLowerCase())
                || val.city.toLocaleLowerCase().includes(e.target.value.toLocaleLowerCase()) || val.property_type.toLocaleLowerCase().includes(e.target.value.toLocaleLowerCase())
            )
            setPropertyData(searchData)
        }
        else {
            setPropertyData(filterData)
        }
    }

    const gridStyles = {
        padding: 3,
    };

    return (

        <Grid container spacing={2} justifyContent="center" sx={gridStyles}>
            <Grid item lg={12} xs={11}>
                {/* <Toolbar/> */}
                <Toolbar />
                <h1>Warehouse Property</h1>
                <Paper variant="outlined" sx={{
                    padding: '1em',
                    width: '100%'
                }}>
                    <Grid item lg={12} xs={11}>

                        <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <Typography variant="subtitle1" sx={{ fontSize: "20px" }} gutterBottom>Property List</Typography>
                            <div className='search_field'>
                                <SearchIcon />
                                <input type="search" placeholder='Search here.....' value={searchTerm} onChange={(e) => handleSearch(e)} />
                            </div>
                        </Box>
                    </Grid>

                    <Grid item lg={12} xs={11}>
                        <TableContainer component={Paper}>

                            <Table sx={{ minWidth: 500 }} aria-label="custom pagination table">
                                <TableHead>
                                    <TableRow>
                                        <StyledTableCell>Property Name</StyledTableCell>
                                        <StyledTableCell>Property Survay No</StyledTableCell>
                                        <StyledTableCell>Property Type</StyledTableCell>
                                        <StyledTableCell>Company Name</StyledTableCell>
                                        <StyledTableCell>Address</StyledTableCell>
                                        <StyledTableCell>City</StyledTableCell>
                                        <StyledTableCell>Zip Code</StyledTableCell>
                                        <StyledTableCell>Country</StyledTableCell>
                                        <StyledTableCell>State</StyledTableCell>
                                        <StyledTableCell>Total Galas</StyledTableCell>
                                        <StyledTableCell>Gala Details</StyledTableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {
                                        loader ?

                                            <TableRow>
                                                <TableCell colSpan={10} style={{ height: '250px', textAlign: 'center', fontSize: '20px' }}>
                                                    <CircularProgress />
                                                </TableCell>
                                            </TableRow>

                                            :
                                            propertyData.length > 0 ?
                                                (rowsPerPage > 0
                                                    ? propertyData.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                                    : propertyData
                                                ).map((e, i) => {
                                                    return (
                                                        <TableRow key={i}>
                                                            <TableCell component="th" scope="row">
                                                                {e.property_name}
                                                            </TableCell>
                                                            <TableCell component="th" scope="row">
                                                                {e.property_survey_number}
                                                            </TableCell>
                                                            <TableCell component="th" scope="row">
                                                                {e.property_type}
                                                            </TableCell>
                                                            <TableCell component="th" scope="row">
                                                                {e.company}
                                                            </TableCell>
                                                            <TableCell component="th" scope="row">
                                                                {e.address}
                                                            </TableCell>
                                                            <TableCell component="th" scope="row">
                                                                {e.city}
                                                            </TableCell>
                                                            <TableCell component="th" scope="row">
                                                                {e.zipcode}
                                                            </TableCell>
                                                            <TableCell component="th" scope="row">
                                                                {e.country}
                                                            </TableCell>
                                                            <TableCell component="th" scope="row">
                                                                {e.state}
                                                            </TableCell>
                                                            <TableCell component="th" scope="row">
                                                                {e.total_gala}
                                                            </TableCell>

                                                            <TableCell component="th" scope="row">
                                                                {e.total_gala === 0 ? "-" :
                                                                    <Link to={`/property/all_property/${e.uid}`} style={{ textDecoration: "none" }}>
                                                                        <Button size='small' variant="text" sx={{ textTransform: 'lowercase' }} endIcon={<Visibility />}>
                                                                            view
                                                                        </Button>
                                                                    </Link>
                                                                }

                                                            </TableCell>
                                                        </TableRow>
                                                    )
                                                })
                                                :
                                                <TableRow>
                                                    <TableCell colSpan={10} style={{ height: '250px', textAlign: 'center', fontSize: '20px' }}>
                                                        No data found
                                                    </TableCell>
                                                </TableRow>

                                    }

                                    {emptyRows > 0 && (
                                        <TableRow style={{ height: 53 * emptyRows }}>
                                            <TableCell colSpan={6} />
                                        </TableRow>
                                    )}
                                </TableBody>
                                <TableFooter>
                                    <TableRow>
                                        {
                                            propertyData.length > 0 ?
                                                <TablePagination
                                                    rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
                                                    colSpan={12}
                                                    count={propertyData.length}
                                                    rowsPerPage={rowsPerPage}
                                                    page={page}
                                                    SelectProps={{
                                                        inputProps: {
                                                            'aria-label': 'rows per page',
                                                        },
                                                        native: true,
                                                    }}
                                                    onPageChange={handleChangePage}
                                                    onRowsPerPageChange={handleChangeRowsPerPage}
                                                    ActionsComponent={TablePaginationActions}
                                                />
                                                : ""
                                        }
                                    </TableRow>
                                </TableFooter>
                            </Table>
                        </TableContainer>

                    </Grid>
                </Paper>
            </Grid>
        </Grid>
    );
}


