import React, { useEffect, useState } from 'react'
import { Grid, Toolbar, Paper, Box, Typography, TextField, Button, Autocomplete } from '@mui/material'
import { useForm, Controller } from 'react-hook-form';
// import { useRouter } from 'next/ ';
import Cookies from 'js-cookie';
import axios from 'axios'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Warehouse } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
export default function Contract_with_farmers() {
    const accessToken = Cookies.get("accessToken")
    const companyType = Cookies.get("companyName")

    // const router = useRouter()
    const naviagte = useNavigate()
    const { register, handleSubmit, reset, formState: { errors }, control, setValue } = useForm()
    const [allWarehouse, setAllWarehouse] = useState([])
    const [allFarmer, setAllFarmer] = useState([])
    const [loader, setLoader] = useState(false)
    const [warehouseId, setWarehouseId] = useState("")
    const [farmerId, setFarmerId] = useState("")
    useEffect(() => {
        const userAccessToken = Cookies.get("accessToken")
        if (!userAccessToken) {
            // router.push('/')
            naviagte('/')
        }
    }, [naviagte])



    // ******* get all user start *******

    const getFarmer = async () => {
        await axios({
            method: "get",
            url: `${process.env.REACT_APP_NEXT_PUBLIC_BASE_URL}/get-farmer-list/?company_type=${companyType}`,
            headers: {
                Authorization: `Bearer ${accessToken}`,
                'Content-Type': 'application/json'
            }
        })
            .then((res) => {
                console.log("get farmer res----", res.data.response)
                setAllFarmer(res.data.response)
            })
            .catch((error) => {
                console.log("error", error)
            })
    }

    useEffect(() => {
        getFarmer()
    }, [accessToken])
    // ******* get all user end *******

    // ******* get all warehouse start ******
    const getAllWarehouse = async () => {
        await axios({
            method: "get",
            url: `${process.env.REACT_APP_NEXT_PUBLIC_BASE_URL}/get-owner-warehouse-list/?company_type=${companyType}`,
            headers: {
                Authorization: `Bearer ${accessToken}`,
                'Content-Type': 'application/json'
            }
        })
            .then((res) => {
                console.log("get warehouse res----", res.data.response)
                setAllWarehouse(res.data.response)

            })
            .catch((error) => {
                console.log("error", error)
            })
    }

    useEffect(() => {
        getAllWarehouse()
    }, [accessToken])
    // ******* get all warehouse end *******



    // ******* add contract start *******
    const onSubmit = (data) => {
        setLoader(true)

        let formData = new FormData()
        if (warehouseId) {
            formData.append("warehouse", warehouseId.uid)
        }
        if (farmerId) {
            formData.append("user", farmerId.user_uid)
        }

        axios.post(`${process.env.REACT_APP_NEXT_PUBLIC_BASE_URL}/create-contract-with-farmer/`, formData,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                    'Content-Type': 'multipart/form-data'
                }
            }
        )
            .then((res) => {
                console.log("add contract res----", res.data)
                if (res) {
                    setLoader(false)
                    toast.success('Contract successful created with farmer', {
                        position: "top-center",
                        autoClose: 2500,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                        theme: "colored",
                    });

                    setTimeout(() => {
                        reset()
                        naviagte("/farmers/farmers_property")
                        document.getElementsByClassName('required_farmer_field')[0].getElementsByTagName("button")[0].click()
                        document.getElementsByClassName('required_warehouse_field')[0].getElementsByTagName("button")[0].click()
                    }, 2500)
                }
            })
            .catch((error) => {
                console.log("error", error)
                setLoader(false)
            })
    }
    // ******* add contract end *******


    const gridStyles = {
        padding: 3,
    };

    return (
        <>
            {loader ? <div className="loader_wrap"><div className="loader"></div></div> : <div></div>}
            <Grid container spacing={2} sx={gridStyles}>
                <Grid item lg={12}>
                    <Toolbar />
                    <h1>Add Contract With Farmers</h1>
                    <Paper variant="outlined" sx={{
                        padding: '1.5em 1em',
                        width: '100%',
                        marginTop: "2em"
                    }}>

                        <form onSubmit={handleSubmit(onSubmit)}>
                            <Typography variant="body1" sx={{ fontSize: "23px" }} gutterBottom>Contract Detail</Typography>

                            <Box sx={{
                                '& .MuiTextField-root': { m: 1, width: '87ch' },
                            }}
                                style={{ display: "flex", flexWrap: "wrap", marginTop: "10px", alignItems: "center" }}
                                noValidate
                                autoComplete="off">

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "500", paddingLeft: "10px" }}>Warehouse</h4>
                                    <div className='formGroup'>
                                        <Controller
                                            name="warehouse"
                                            control={control}
                                            rules={{
                                                required: "Required"
                                            }}
                                            render={({ field, fieldState }) => {
                                                return (
                                                    <Autocomplete
                                                        {...field}
                                                        options={allWarehouse}
                                                        getOptionLabel={(e) => e.property_name || ""}
                                                        renderInput={(params) => (
                                                            <TextField
                                                                {...params}
                                                                label="Select Warehouse"
                                                                variant="outlined"
                                                                error={!!fieldState.error}
                                                                helperText={fieldState.error?.message}
                                                                className="required_warehouse_field"
                                                            />
                                                        )}
                                                        onChange={(_, data) => {
                                                            field.onChange(data)
                                                            setWarehouseId(data)
                                                        }}
                                                    />
                                                );
                                            }}
                                        />
                                    </div>

                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "500", paddingLeft: "10px" }}>Farmer</h4>
                                    <div className='formGroup'>
                                        <Controller
                                            name="farmer"
                                            control={control}
                                            rules={{
                                                required: "Required"
                                            }}
                                            render={({ field, fieldState }) => {
                                                return (
                                                    <Autocomplete
                                                        {...field}
                                                        options={allFarmer}
                                                        getOptionLabel={(e) => e.username || ""}
                                                        renderInput={(params) => (
                                                            <TextField
                                                                {...params}
                                                                label="Select Farmer"
                                                                variant="outlined"
                                                                error={!!fieldState.error}
                                                                helperText={fieldState.error?.message}
                                                                className="required_farmer_field"
                                                            />
                                                        )}
                                                        onChange={(_, data) => {
                                                            field.onChange(data)
                                                            setFarmerId(data)
                                                        }}
                                                    />
                                                );

                                            }}
                                        />
                                    </div>

                                </Box>
                            </Box>
                            <Box style={{ marginTop: "20px", marginRight: "12px", display: "flex", justifyContent: "flex-end" }}>
                                <Button variant="contained" type="submit" sx={{ minWidth: "100px", fontSize: "1rem" }}>Add</Button>
                            </Box>

                        </form>
                    </Paper>
                </Grid>
            </Grid>

            <ToastContainer
                position="top-center"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="colored"
            />
        </>
    )
}









