
import React, { useEffect, useState } from 'react'
import { Grid, Toolbar, Paper, Box, Typography, TextField, Button } from '@mui/material'
import axios from 'axios';
// import { useRouter } from 'next/router';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
// import Link from 'next/link';
import { Link, useNavigate, useParams } from 'react-router-dom';
import Cookies from 'js-cookie';

export default function TenantFormDetail() {
    // const router = useRouter()
   
    const navigate = useNavigate()
    const pathRoute = useParams()
    const { slug, id } = pathRoute

    const accessToken = Cookies.get("accessToken")

    useEffect(() => {
        const userAccessToken = Cookies.get("accessToken")
        if (!userAccessToken) {
            // router.push('/')
            navigate('/')
        }
    }, [navigate])

    const companyType = Cookies.get("companyName")
    const [contractData, setContractData] = useState()
    const getInvestorDetails = () => {
        axios.get(`${process.env.REACT_APP_NEXT_PUBLIC_BASE_URL}/get-investor-rental-gala-detail-view/${id}/?company_type=${companyType}`,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                    'Content-Type': 'multipart/form-data'
                }
            }
        )
            .then((res) => {
                console.log("investor contract gala details---", res.data.response)
                setContractData(res.data.response)
            })
            .catch((err) => {
                console.log("err", err)
            })
    }
    useEffect(() => {
        getInvestorDetails()
    }, [id, navigate])


    const gridStyles = {
        padding: 3,
    };

    return (
        <>

            <Grid container spacing={2} sx={gridStyles}>
                <Grid item lg={12}>
                    <Toolbar />
                    <Grid item lg={12} xs={11}>

                        <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <Typography variant="subtitle1" sx={{ fontSize: "22px" }} gutterBottom>View Contract Detail</Typography>
                            <Link to={`/tenants/${slug}`}><Button variant="contained"><ArrowBackIcon /> Back</Button></Link>
                        </Box>
                    </Grid>
                    <Paper variant="outlined" sx={{
                        padding: '1.5em 1em',
                        width: '100%',
                        marginTop: "1em"
                    }}>

                        <form className='farmer_detail_form'>

                            <Box sx={{
                                '& .MuiTextField-root': { m: 1, width: '85ch' },
                            }}
                                style={{ display: "flex", flexWrap: "wrap" }}
                                noValidate
                                autoComplete="off">
                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Investor</h4>
                                    <TextField id="outlined-read-only-input" label={contractData && contractData.owner.username} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Rental</h4>
                                    <TextField id="outlined-read-only-input" label={contractData && contractData.user.username} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Gala</h4>
                                    <TextField id="outlined-read-only-input" label={contractData && contractData.gala.gala_number} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Agreement type</h4>
                                    <TextField id="outlined-read-only-input" label={contractData && contractData.agreement_type} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Agreement Start Date</h4>
                                    <TextField id="outlined-read-only-input" label={contractData && contractData.agreement_valid_start_date} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Agreement End Date</h4>
                                    <TextField id="outlined-read-only-input" label={contractData && contractData.agreement_valid_end_date} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Ghar Patti Start Date</h4>
                                    <TextField id="outlined-read-only-input" label={contractData && contractData.ghar_patti_start_date} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Ghar Patti End Date</h4>
                                    <TextField id="outlined-read-only-input" label={contractData && contractData.ghar_patti_end_date} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "45px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Agreement Doc - <a href="" target="_blank">View and Download</a></h4>
                                    <br></br>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Ghar Patti Doc - <a href="" target="_blank">View and Download</a></h4>
                                </Box>
                            </Box>
                        </form>
                    </Paper>
                </Grid>
            </Grid>
        </>
    )
}


