
import React, { useEffect, useState } from 'react'
import { Grid, Toolbar, Paper, Box, Typography, TextField, Button } from '@mui/material'
import { useForm } from 'react-hook-form';
import axios from 'axios';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import Cookies from 'js-cookie';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Link, useNavigate, useParams } from 'react-router-dom';

export default function FarmerDetailForm() {
   const navigate = useNavigate()
   const path = useParams()
    const { slug, id } = path
    const accessToken = Cookies.get("accessToken")
    const [farmerGalaDetail, setFarmerGalaDetail] = useState()
    const companyType = Cookies.get("companyName")
    useEffect(() => {
        const userAccessToken = Cookies.get("accessToken")
        if (!userAccessToken) {
            navigate('/')
        }
    }, [navigate])

const getFarmerGalaDetails = ()=>{
    axios.get(`${process.env.REACT_APP_NEXT_PUBLIC_BASE_URL}/get-farmer-gala-detail/${id}/?company_type=${companyType}`,
    {
        headers: {
            Authorization: `Bearer ${accessToken}`,
            'Content-Type': 'multipart/form-data'
        }
    }
    )
    .then((res)=>{
        console.log("farmer gala details---", res.data.response)
        setFarmerGalaDetail(res.data.response)
    })
    .catch((err)=>{
        console.log("err", err)
    })
}
useEffect(()=>{
    getFarmerGalaDetails() 
}, [id, navigate])
    const gridStyles = {
        padding: 3,
    };


    // console.log("farmerGalaDetail", farmerGalaDetail)
    return (
        <>

            <Grid container spacing={2} sx={gridStyles}>
                <Grid item lg={12}>
                    <Toolbar />
                    <Grid item lg={12} xs={11}>

                        <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <Typography variant="subtitle1" sx={{ fontSize: "22px" }} gutterBottom>View Contract Detail</Typography>
                            <Link to={`/farmers/farmers_property/${slug}`}><Button variant="contained"><ArrowBackIcon /> Back</Button></Link>
                        </Box>
                    </Grid>
                    <Paper variant="outlined" sx={{
                        padding: '1.5em 1em',
                        width: '100%',
                        marginTop: "1em"
                    }}>

                        <form className='farmer_detail_form'>

                            <Box sx={{
                                '& .MuiTextField-root': { m: 1, width: '85ch' },
                            }}
                                style={{ display: "flex", flexWrap: "wrap" }}
                                noValidate
                                autoComplete="off">
                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Farmer</h4>
                                    <TextField id="outlined-read-only-input" label={farmerGalaDetail && farmerGalaDetail.owner.username} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Rental</h4>
                                    <TextField id="outlined-read-only-input" label={farmerGalaDetail && farmerGalaDetail.user.username} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Gala</h4>
                                    <TextField id="outlined-read-only-input" label={farmerGalaDetail && farmerGalaDetail.gala.gala_number} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Agreement type</h4>
                                    <TextField id="outlined-read-only-input" label={farmerGalaDetail && farmerGalaDetail.agreement_type} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Agreement Start Date</h4>
                                    <TextField id="outlined-read-only-input" label={farmerGalaDetail && farmerGalaDetail.agreement_valid_start_date} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Agreement End Date</h4>
                                    <TextField id="outlined-read-only-input" label={farmerGalaDetail && farmerGalaDetail.agreement_valid_end_date} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Ghar Patti Start Date</h4>
                                    <TextField id="outlined-read-only-input" label={farmerGalaDetail && farmerGalaDetail.ghar_patti_start_date} variant="outlined" disabled />
                                </Box>

                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Ghar Patti End Date</h4>
                                    <TextField id="outlined-read-only-input" label={farmerGalaDetail && farmerGalaDetail.ghar_patti_end_date}  variant="outlined" disabled />
                                </Box>
                                <Box style={{ marginTop: "15px" }}>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Agreement Doc - <a href="" target="_blank">View and Download</a></h4>
                                    <br></br>
                                    <h4 style={{ fontWeight: "400", paddingLeft: "10px" }}>Ghar Patti Doc - <a href="" target="_blank">View and Download</a></h4>
                                </Box>
                            </Box>
                        </form>
                    </Paper>
                </Grid>
            </Grid>
            <ToastContainer
                position="top-center"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="colored"
            />
        </>
    )
}


