import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useTheme, styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import TableHead from '@mui/material/TableHead';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableFooter from '@mui/material/TableFooter';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import FirstPageIcon from '@mui/icons-material/FirstPage';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import LastPageIcon from '@mui/icons-material/LastPage';
import { Grid, Toolbar, Button, Typography, CircularProgress } from '@mui/material';
// import Link from 'next/link';
import { Visibility } from '@mui/icons-material';
// import { useRouter } from 'next/router';
import axios from 'axios';
import Cookies from 'js-cookie';
import Tooltip from '@mui/material/Tooltip';
import { useNavigate } from 'react-router-dom';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.primary.dark,
        color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
    },
}));

function TablePaginationActions(props) {
    const theme = useTheme();
    const { count, page, rowsPerPage, onPageChange } = props;

    const handleFirstPageButtonClick = (event) => {
        onPageChange(event, 0);
    };

    const handleBackButtonClick = (event) => {
        onPageChange(event, page - 1);
    };

    const handleNextButtonClick = (event) => {
        onPageChange(event, page + 1);
    };

    const handleLastPageButtonClick = (event) => {
        onPageChange(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
    };


    return (
        <Box sx={{ flexShrink: 0, ml: 2.5 }}>
            <IconButton
                onClick={handleFirstPageButtonClick}
                disabled={page === 0}
                aria-label="first page"
            >
                {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
            </IconButton>
            <IconButton
                onClick={handleBackButtonClick}
                disabled={page === 0}
                aria-label="previous page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            </IconButton>
            <IconButton
                onClick={handleNextButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="next page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </IconButton>
            <IconButton
                onClick={handleLastPageButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="last page"
            >
                {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
            </IconButton>
        </Box>
    );
}

TablePaginationActions.propTypes = {
    count: PropTypes.number.isRequired,
    onPageChange: PropTypes.func.isRequired,
    page: PropTypes.number.isRequired,
    rowsPerPage: PropTypes.number.isRequired,
};


export default function WaterSupplyPage() {
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(5);
    const [waterSupply, setWaterSupply] = useState([])
    // Avoid a layout jump when reaching the last page with empty rows.
    const emptyRows =
        page > 0 ? Math.max(0, (1 + page) * rowsPerPage - waterSupply.length) : 0;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const accessToken = Cookies.get("accessToken")
    const companyType = Cookies.get("companyName")
    // const router = useRouter()
    const navigate = useNavigate()

    const [loader, setLoader] = useState(true)
    useEffect(() => {
        const userAccessToken = Cookies.get("accessToken")
        if (!userAccessToken) {
            // router.push('/')
            navigate('/')
        }
    }, [navigate])

    const getWaterSupply = () => {
        axios.get(`${process.env.REACT_APP_NEXT_PUBLIC_BASE_URL}/get-allrentals-service-request/?company_type=${companyType}&service_type=Water Supply`,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                    'Content-Type': 'application/json'
                }
            }
        )
            .then((res) => {
                console.log("water supply list res", res.data.response)
                if (res) {
                    setWaterSupply(res.data.response)
                    setLoader(false)
                }
            })
            .catch((err) => {
                console.log("water supply list err", err)
                setLoader(false)
            })
    }
    useEffect(() => {
        getWaterSupply()
    }, [navigate])

    const gridStyles = {
        padding: 3,
    };

    return (

        <Grid container spacing={2} justifyContent="center" sx={gridStyles}>
            <Grid item lg={12} xs={11}>
                {/* <Toolbar /> */}
                <Toolbar />
                <h1>Water Supply</h1>
                <Paper variant="outlined" sx={{
                    padding: '1em',
                    width: '100%'
                }}>
                    <Grid item lg={12} xs={11}>

                        <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <Typography variant="subtitle1" sx={{ fontSize: "20px" }} gutterBottom>Water Supply Service Request List</Typography>
                        </Box>
                    </Grid>
                    <Grid item lg={12} xs={11}>
                        <TableContainer component={Paper} sx={{ marginTop: "15px" }}>

                            <Table sx={{ minWidth: 500 }} aria-label="custom pagination table">
                                <TableHead>
                                    <TableRow>
                                        <StyledTableCell>Tracking ID</StyledTableCell>
                                        <StyledTableCell>Request Date</StyledTableCell>
                                        <StyledTableCell>Name</StyledTableCell>
                                        <StyledTableCell>Service Name</StyledTableCell>
                                        <StyledTableCell>Gala Number</StyledTableCell>
                                        <StyledTableCell>Warehouse Name</StyledTableCell>
                                        <StyledTableCell>Description</StyledTableCell>
                                        <StyledTableCell>Status</StyledTableCell>
                                        {/* <StyledTableCell>Detail</StyledTableCell> */}
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {
                                        loader ?

                                            <TableRow>
                                                <TableCell colSpan={10} style={{ height: '250px', textAlign: 'center', fontSize: '20px' }}>
                                                    <CircularProgress />
                                                </TableCell>
                                            </TableRow>

                                            :
                                            waterSupply.length > 0 ?
                                                (rowsPerPage > 0
                                                    ? waterSupply.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                                    : waterSupply
                                                ).map((e, i) => {
                                                    return (
                                                        <TableRow key={i}>
                                                        <TableCell component="th" scope="row">
                                                            {e.tracking_id}
                                                        </TableCell>
                                                        <TableCell component="th" scope="row">
                                                            {e.service_request_date}
                                                        </TableCell>
                                                        <TableCell component="th" scope="row">
                                                            {e.user.username}
                                                        </TableCell>
                                                        <TableCell component="th" scope="row">
                                                             {e.request_sub_service.sub_service_name}
                                                        </TableCell>
                                                        <TableCell component="th" scope="row">
                                                            {e.gala.gala_number}
                                                        </TableCell>
                                                        <TableCell component="th" scope="row">
                                                            {e.gala.warehouse.property_name}
                                                        </TableCell>
                                                        <TableCell component="th" scope="row">
                                                            {e.description}
                                                        </TableCell>
                                                        <TableCell component="th" scope="row">
                                                            {e.status}
                                                        </TableCell>
                                                        {/* <TableCell component="th" scope="row">

                                                        </TableCell> */}
                                                    </TableRow>
                                                    )
                                                })
                                                :
                                                <TableRow>
                                                    <TableCell colSpan={10} style={{ height: '250px', textAlign: 'center', fontSize: '20px' }}>
                                                        No data found
                                                    </TableCell>
                                                </TableRow>

                                    }

                                    {emptyRows > 0 && (
                                        <TableRow style={{ height: 53 * emptyRows }}>
                                            <TableCell colSpan={9} />
                                        </TableRow>
                                    )}
                                </TableBody>
                                <TableFooter>
                                    <TableRow>
                                        {
                                            waterSupply.length > 0 ?
                                                <TablePagination
                                                    rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
                                                    colSpan={12}
                                                    count={waterSupply.length}
                                                    rowsPerPage={rowsPerPage}
                                                    page={page}
                                                    SelectProps={{
                                                        inputProps: {
                                                            'aria-label': 'rows per page',
                                                        },
                                                        native: true,
                                                    }}
                                                    onPageChange={handleChangePage}
                                                    onRowsPerPageChange={handleChangeRowsPerPage}
                                                    ActionsComponent={TablePaginationActions}
                                                />
                                                : ""
                                        }

                                    </TableRow>
                                </TableFooter>
                            </Table>
                        </TableContainer>
                    </Grid>
                </Paper>
            </Grid>
        </Grid>
    );
}



